// ResponsiveTabs.jsx

import React, { useState, useCallback, Suspense, useEffect } from 'react';
import AppBar from '@mui/material/AppBar';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import MenuIcon from '@mui/icons-material/Menu';
import Drawer from '@mui/material/Drawer'; // Add this import statement
import { styled, createTheme, ThemeProvider } from '@mui/material/styles';
import ScrollProgressBar from './ScrollProgressBar';


// Lazy-loaded components
const LazyDrawerAppBarRequest = React.lazy(() => import('./CaptionRequ'));
const LazyDrawerAppBarApprove = React.lazy(() => import('./Approve'));
const LazyDrawerAppBar = React.lazy(() => import('./Status'));

const drawerWidth = 100;

const theme = createTheme({
  typography: {
    fontFamily: 'Dot Matrix',
  },
  palette: {
    mode: 'light',
    primary: {
      main: '#000000',
      light: '#888888',
    },
    secondary: {
      main: '#ffffff',
    },
  },

});

const Root = styled('div')({
  display: 'flex',
  height: '100vh',
  color: 'white',

});

const StyledAppBar = styled(AppBar)(({ theme }) => ({
 // zIndex: theme.zIndex.drawer + 1,
  backgroundColor: 'rgba(0, 0, 0, 0.1)',
    width: '100vh'
  //justifyContent: 'flex-end',
}));

const StyledIconButton = styled(IconButton)({
  marginLeft: 'auto',
  paddingRight: '28px',
});

const StyledDrawer = styled(Drawer)({
  width: drawerWidth,
  flexShrink: 0,
});

const StyledContent = styled('div')({
  flexGrow: 1,
  padding: theme => theme.spacing(3),
  marginLeft: 0,
  transition: theme.transitions.create(['margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
});

export default function ResponsiveTabs() {
  const [value, setValue] = useState(0);
  const [drawerOpen, setDrawerOpen] = useState(false);

  const openDrawer = useCallback(() => {
    setDrawerOpen(true);
  }, []);

  const closeDrawer = useCallback(() => {
    setDrawerOpen(false);
  }, []);

  const handleChange = useCallback(
    (event, newValue) => {
      setValue(newValue);
      if (window.innerWidth < 600) {
        closeDrawer();
      }
    },
    [closeDrawer]
  );

  return (
    <ThemeProvider theme={theme}>
      <Root>
        <ScrollProgressBar />
        <StyledAppBar position="fixed">
          {window.innerWidth < 600 && (
            <StyledIconButton
              color="inherit"
              aria-label="open drawer"
              onClick={openDrawer}
            >
              <MenuIcon />
            </StyledIconButton>
          )}
        </StyledAppBar>

        <StyledDrawer
          variant={window.innerWidth < 600 ? 'temporary' : 'permanent'}
          anchor="right"
          open={drawerOpen}
          onClose={closeDrawer}
          aria-hidden={!drawerOpen}
        >
          <List>
            <ListItem button onClick={() => handleChange(null, 0)}>
              <ListItemText primary="👤 About me" />
            </ListItem>
          </List>
        </StyledDrawer>

        <StyledContent
          style={{
            marginLeft: drawerOpen ? `${drawerWidth}px` : 0,
          }}
        >
          {value === 0 && (
            <Suspense fallback={<div>Loading...</div>}>
              <LazyDrawerAppBarRequest />
            </Suspense>
          )}
          {value === 1 && (
            <Suspense fallback={<div>Loading...</div>}>
              <LazyDrawerAppBarApprove />
            </Suspense>
          )}
          {value === 2 && (
            <Suspense fallback={<div>Loading...</div>}>
              <LazyDrawerAppBar />
            </Suspense>
          )}
        </StyledContent>
      </Root>
    </ThemeProvider>
  );
}
