// Nav.jsx
import React from 'react';
import { AppBar, Typography, Toolbar, useScrollTrigger, Slide, IconButton, Box } from "@mui/material";
import { styled } from '@mui/material/styles';

function ElevationScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  });
}


const ColorChangingText = ({ children }) => (
  <span style={{ transition: 'color 0.3s ease-in-out' }}>{children}</span>
);

const Nav = (props) => {
  return (
  <ElevationScroll>
       <Box sx={{ flexGrow: 1 , display: { sm: 'block' }}}>
         <AppBar position="sticky" sx={{ backgroundColor: 'rgba(255, 255, 255, 0.8)'}}>

             <IconButton disabled>
               <Typography
                 variant="h5"
                 noWrap
                 component="div"
                  sx={{
                                   flexGrow: 1,
                                   display: { sm: 'block' },
                                   color: 'grey',
                                 }}
               >
                 <ColorChangingText>
                   {(props.props.text[0])}
                  </ColorChangingText>
               </Typography>
             </IconButton>

         </AppBar>
       </Box>
     </ElevationScroll>
  );
}

export default Nav;
