
import React, { useState, useEffect } from 'react';
import Typography from '@mui/material/Typography';
import Toolbar from '@mui/material/Toolbar';

function ScrollProgressBar() {
  const [scrollProgress, setScrollProgress] = useState(0);

  const handleScroll = () => {
    const scrollHeight = document.documentElement.scrollHeight - window.innerHeight;
    const scrollTop = window.scrollY;
    const progress = (scrollTop / scrollHeight) * 100;

    if (progress <= 101 && progress >= 100) {
      setScrollProgress(100);
    }
    if (progress <= 100) {
      setScrollProgress(progress);
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const toolbarStyles = {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    zIndex: 1000,
    width: '100%',
    height: '20px',
    padding: '8px 16px',
    borderBottom: '1px solid #000',
    background: 'linear-gradient(to right, #000, #333)',
  };

  const progressBarStyles = {
    position: 'fixed',
    top: 0,
    left: 0,
    height: '2px',
    width: '100%',
    maxWidth: '100vw', // Added a maxWidth for small screens.
    background: '#FF0000',
    transform: `scaleX(${scrollProgress / 100})`, // Scale the width of the progress bar based on scroll progress.
    transformOrigin: 'left',
    transition: 'transform 0.3s ease-in-out',
  };

  return (
    <div>
      <Toolbar elevation={12} style={toolbarStyles}>
        <Typography variant="h6" color="textSecondary" style={{ color: '#FF0000' }}>
          Progress: {scrollProgress.toFixed(2)}%
        </Typography>
      </Toolbar>

      <div style={progressBarStyles} />
    </div>
  );
}

export default ScrollProgressBar;
