
import * as React from 'react';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import './App.css';
import Nav from './components/Nav';
import FullWidthTabs from './components/Tab';
import About from './components/About';
import ProminentAppBar from './components/PromientAppBar';
import VerticalTabs from './components/Request';
import SwipeableEdgeDrawer from './components/Chatbot';
import WebFont from 'webfontloader';
import './fonts/dotmatri.ttf';

const { palette } = createTheme();
const { augmentColor } = palette;
const createColor = (mainColor) => augmentColor({ color: { main: mainColor } });

const darkTheme = createTheme({
  typography: {
    fontFamily: 'Dot Matrix',
  },
  palette: {
    mode: 'light',
    primary: {
      main: '#000000',
      light: '#888888',
    },
    secondary: {
      main: '#ffffff',
    },
    black: createColor('#888888'),
    grey: createColor("#6C767D"),
    yellow: createColor('#ffd300'),
    green: createColor('#46C016'),
    red : createColor('#888888'),
    
  },
});
function App() {
  const text =  {text:["I love to play video games :) "]}
  return (
   
    <div className="App gradient-background">
       <ThemeProvider theme={darkTheme}>
      <About></About>
      <Nav props={text}></Nav>
      <VerticalTabs></VerticalTabs>
      </ThemeProvider>
    </div>
   

  );
}

WebFont.load({
    custom: {
      families: ['Dot Matrix'],
    },
  });

export default App;
